import React from 'react';
import { Box, Anchor } from 'grommet';

export default () => (
  <Box
    tag='footer'
    direction='row'
    justify='center'
    pad={{ top: 'medium' }}
    justifySelf={true}
  >
    <Box
      basis='large'
      border='top'
      direction='row'
      justify='center'
      pad='medium'
      gap='medium'
    >
      made with
      <Anchor
        href='https://github.com/grommet/grommet/tree/NEXT'
        target='_blank'
        label='grommet v2'
        a11yTitle='Go to the github page for Grommet 2'
      />
      based on
      <Anchor
        href='https://github.com/atanasster/grommet-nextjs'
        target='_blank'
        label='atanasster/grommet-nextjs'
        a11yTitle='Go to the github page for next-sample'
      />
    </Box>
  </Box>
);



